@use 'variables';

nav{
    z-index: 1;
    align-self: flex-start;
    background-color: variables.$first-color;
    list-style: none;
    color: variables.$white-color;
    position: absolute;
    right: 0;
}

.burger{
    position: absolute;
    cursor: pointer;
    right: variables.$unit;
    top: variables.$unit;
}

.cross{
    position: absolute;
    cursor: pointer;
    right: variables.$unit;
    top: variables.$unit;
    display: none;
}

nav ul{
    padding: 0;
    margin: 0;
}

.menu{
    display: none;
}

.active{
    display: block;
}

.hidden{
    display: none;
}

.menu-icon{
    display: flex;
    justify-content: flex-end;
    margin: 8px 16px;
    cursor: pointer;
}

nav ul ul{
    padding: 0;
}

nav ul li{
    display: none;
    padding: 0;
}
.unwrap{
    margin: 20px 0 0 0;
    font-weight: 600;
    cursor: pointer;
}

.unwrap p{
    padding-left: variables.$xl-space;
}

.unwrap:hover p{
    background-color: variables.$white-color;
    color: variables.$first-color;
}

